"use client";

import { useState, useEffect } from "react";
import { supabase } from "@/lib/supabase";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
  CardDescription,
  CardFooter,
} from "@/components/ui/card";
import { useToast } from "@/hooks/use-toast";
import {
  Loader2,
  LogOut,
  Image as ImageIcon,
  Save,
  Upload,
} from "lucide-react";
import Image from "next/image";

export default function AdminPage() {
  const [session, setSession] = useState<any>(null);
  const [loadingLogin, setLoadingLogin] = useState(false);
  const [uploadingHero, setUploadingHero] = useState(false);
  const [uploadingAbout, setUploadingAbout] = useState(false);

  // Login States
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  // Estado das Imagens (URL do banco de dados)
  const [currentHeroUrl, setCurrentHeroUrl] = useState<string | null>(null);
  const [currentAboutUrl, setCurrentAboutUrl] = useState<string | null>(null);

  // Estado dos Arquivos Selecionados (Para preview local antes de subir)
  const [heroFile, setHeroFile] = useState<File | null>(null);
  const [aboutFile, setAboutFile] = useState<File | null>(null);

  const { toast } = useToast();

  // 1. Verificar Sessão e Buscar Imagens Atuais
  useEffect(() => {
    supabase.auth.getSession().then(({ data: { session } }) => {
      setSession(session);
      if (session) fetchCurrentImages();
    });

    const {
      data: { subscription },
    } = supabase.auth.onAuthStateChange((_event, session) => {
      setSession(session);
      if (session) fetchCurrentImages();
    });

    return () => subscription.unsubscribe();
  }, []);

  const fetchCurrentImages = async () => {
    const { data } = await supabase.from("site_config").select("*");
    if (data) {
      const hero = data.find((item) => item.key === "hero_image")?.image_url;
      const about = data.find((item) => item.key === "about_image")?.image_url;
      if (hero) setCurrentHeroUrl(hero);
      if (about) setCurrentAboutUrl(about);
    }
  };

  // 2. Lógica de Login/Logout
  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoadingLogin(true);
    const { error } = await supabase.auth.signInWithPassword({
      email,
      password,
    });
    if (error) {
      toast({
        variant: "destructive",
        title: "Erro no login",
        description: error.message,
      });
    } else {
      toast({ title: "Bem-vindo, Dr. John!" });
    }
    setLoadingLogin(false);
  };

  const handleLogout = async () => {
    await supabase.auth.signOut();
  };

  // 3. Função Genérica de Upload
  const handleSaveImage = async (
    file: File | null,
    key: "hero_image" | "about_image",
    setLoading: (v: boolean) => void
  ) => {
    if (!file) return;

    try {
      setLoading(true);
      const fileExt = file.name.split(".").pop();
      const fileName = `${key}-${Date.now()}.${fileExt}`;

      // Upload para o Storage
      const { error: uploadError } = await supabase.storage
        .from("site-assets")
        .upload(fileName, file);

      if (uploadError) throw uploadError;

      // Obter URL Pública
      const {
        data: { publicUrl },
      } = supabase.storage.from("site-assets").getPublicUrl(fileName);

      // Atualizar no Banco de Dados
      const { error: dbError } = await supabase
        .from("site_config")
        .upsert({ key, image_url: publicUrl }, { onConflict: "key" });

      if (dbError) throw dbError;

      toast({
        title: "Imagem salva com sucesso!",
        className: "bg-green-500 text-white",
      });

      // Limpar seleção local e atualizar URL oficial
      if (key === "hero_image") {
        setHeroFile(null);
        setCurrentHeroUrl(publicUrl);
      } else {
        setAboutFile(null);
        setCurrentAboutUrl(publicUrl);
      }
    } catch (error: any) {
      toast({
        variant: "destructive",
        title: "Erro ao salvar",
        description: error.message,
      });
    } finally {
      setLoading(false);
    }
  };

  // Helper para mostrar o preview: Mostra o arquivo local se existir, senão mostra a URL do banco
  const getPreviewUrl = (file: File | null, dbUrl: string | null) => {
    if (file) return URL.createObjectURL(file);
    if (dbUrl) return dbUrl;
    return null;
  };

  // --- RENDERIZAÇÃO ---

  if (!session) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50 p-4">
        <Card className="w-full max-w-md shadow-xl">
          <CardHeader>
            <CardTitle className="text-center">Área Restrita</CardTitle>
            <CardDescription className="text-center">
              Login exclusivo para administração
            </CardDescription>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleLogin} className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="email">Email</Label>
                <Input
                  id="email"
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="password">Senha</Label>
                <Input
                  id="password"
                  type="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                />
              </div>
              <Button
                type="submit"
                className="w-full bg-[#0e2432] hover:bg-[#1a3a4f]"
                disabled={loadingLogin}
              >
                {loadingLogin ? (
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                ) : (
                  "Acessar Painel"
                )}
              </Button>
            </form>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 p-4 md:p-8">
      <div className="max-w-5xl mx-auto space-y-8">
        <div className="flex justify-between items-center bg-white p-4 rounded-lg shadow-sm border">
          <div>
            <h1 className="text-2xl font-bold text-[#0e2432]">
              Gerenciar Imagens
            </h1>
            <p className="text-sm text-gray-500">
              Atualize as fotos do site em tempo real
            </p>
          </div>
          <Button
            variant="ghost"
            className="text-red-600 hover:text-red-700 hover:bg-red-50"
            onClick={handleLogout}
          >
            <LogOut className="mr-2 h-4 w-4" /> Sair
          </Button>
        </div>

        <div className="grid md:grid-cols-2 gap-6">
          {/* --- CARD HERO (CAPA) --- */}
          <Card className="flex flex-col">
            <CardHeader>
              <CardTitle>Capa Principal (Hero)</CardTitle>
              <CardDescription>
                Esta é a primeira imagem que aparece no topo do site.
              </CardDescription>
            </CardHeader>
            <CardContent className="flex-1 space-y-4">
              {/* Área de Preview */}
              <div className="relative aspect-video bg-gray-100 rounded-lg overflow-hidden border-2 border-dashed border-gray-200 group">
                {getPreviewUrl(heroFile, currentHeroUrl) ? (
                  <Image
                    src={getPreviewUrl(heroFile, currentHeroUrl)!}
                    alt="Preview Hero"
                    fill
                    className="object-cover"
                  />
                ) : (
                  <div className="flex flex-col items-center justify-center h-full text-gray-400">
                    <ImageIcon className="h-12 w-12 mb-2" />
                    <span className="text-sm">Nenhuma imagem definida</span>
                  </div>
                )}
                {/* Overlay indicando que é um preview local */}
                {heroFile && (
                  <div className="absolute inset-0 bg-black/40 flex items-center justify-center">
                    <span className="bg-yellow-500 text-white px-3 py-1 rounded-full text-sm font-bold shadow-sm">
                      Pré-visualização (Não salvo)
                    </span>
                  </div>
                )}
              </div>

              {/* Input de Arquivo */}
              <div>
                <Label htmlFor="hero-upload" className="cursor-pointer block">
                  <div className="flex items-center gap-2 p-3 border rounded-md hover:bg-gray-50 transition-colors">
                    <Upload className="h-4 w-4 text-gray-500" />
                    <span className="text-sm text-gray-600">
                      {heroFile
                        ? heroFile.name
                        : "Clique para escolher nova foto..."}
                    </span>
                  </div>
                  <Input
                    id="hero-upload"
                    type="file"
                    accept="image/*"
                    className="hidden"
                    onChange={(e) => setHeroFile(e.target.files?.[0] || null)}
                  />
                </Label>
              </div>
            </CardContent>
            <CardFooter className="bg-gray-50/50 border-t pt-6">
              <Button
                className="w-full bg-teal-600 hover:bg-teal-700"
                onClick={() =>
                  handleSaveImage(heroFile, "hero_image", setUploadingHero)
                }
                disabled={!heroFile || uploadingHero}
              >
                {uploadingHero ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />{" "}
                    Salvando...
                  </>
                ) : (
                  <>
                    <Save className="mr-2 h-4 w-4" /> Salvar Nova Capa
                  </>
                )}
              </Button>
            </CardFooter>
          </Card>

          {/* --- CARD SOBRE --- */}
          <Card className="flex flex-col">
            <CardHeader>
              <CardTitle>Foto "Sobre Mim"</CardTitle>
              <CardDescription>
                A foto que aparece ao lado da sua biografia.
              </CardDescription>
            </CardHeader>
            <CardContent className="flex-1 space-y-4">
              {/* Área de Preview */}
              <div className="relative aspect-square max-w-[300px] mx-auto bg-gray-100 rounded-lg overflow-hidden border-2 border-dashed border-gray-200">
                {getPreviewUrl(aboutFile, currentAboutUrl) ? (
                  <Image
                    src={getPreviewUrl(aboutFile, currentAboutUrl)!}
                    alt="Preview About"
                    fill
                    className="object-cover"
                  />
                ) : (
                  <div className="flex flex-col items-center justify-center h-full text-gray-400">
                    <ImageIcon className="h-12 w-12 mb-2" />
                    <span className="text-sm">Nenhuma imagem</span>
                  </div>
                )}
                {aboutFile && (
                  <div className="absolute inset-0 bg-black/40 flex items-center justify-center">
                    <span className="bg-yellow-500 text-white px-3 py-1 rounded-full text-sm font-bold shadow-sm">
                      Pré-visualização
                    </span>
                  </div>
                )}
              </div>

              {/* Input de Arquivo */}
              <div>
                <Label htmlFor="about-upload" className="cursor-pointer block">
                  <div className="flex items-center gap-2 p-3 border rounded-md hover:bg-gray-50 transition-colors">
                    <Upload className="h-4 w-4 text-gray-500" />
                    <span className="text-sm text-gray-600">
                      {aboutFile
                        ? aboutFile.name
                        : "Clique para escolher nova foto..."}
                    </span>
                  </div>
                  <Input
                    id="about-upload"
                    type="file"
                    accept="image/*"
                    className="hidden"
                    onChange={(e) => setAboutFile(e.target.files?.[0] || null)}
                  />
                </Label>
              </div>
            </CardContent>
            <CardFooter className="bg-gray-50/50 border-t pt-6">
              <Button
                className="w-full bg-teal-600 hover:bg-teal-700"
                onClick={() =>
                  handleSaveImage(aboutFile, "about_image", setUploadingAbout)
                }
                disabled={!aboutFile || uploadingAbout}
              >
                {uploadingAbout ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />{" "}
                    Salvando...
                  </>
                ) : (
                  <>
                    <Save className="mr-2 h-4 w-4" /> Salvar Foto Sobre
                  </>
                )}
              </Button>
            </CardFooter>
          </Card>
        </div>
      </div>
    </div>
  );
}
